<template>
    <div>
        <div class="row">
            <p class="tit">登录</p>
        </div>
        <!-- <div class="row">
            <p></p>
        </div> -->
        <div class="row">
            <el-input v-model="options.username" placeholder="用户名"></el-input>
        </div>
        <div class="row">
            <el-input v-model="options.pwd" placeholder="密码" type="password"></el-input>
        </div>
        <div class="row">
            <el-button type="primary" class="sure" @click="login">确定</el-button>
        </div>
        <div class="row">
            <router-link class="go-reg" tag="p" to="/register">注册</router-link>
        </div>
    </div>
</template>
<script>
    export default {
        data() {
            return {
                options: {
                    username: '',
                    pwd: ''
                }
            }
        },
        methods: {
            login() {
                if (!this.options.username) {
                    return this.$message({
                        message: '请输入用户名',
                        type: 'warning'
                    })
                }
                if (!this.options.pwd) {
                    return this.$message({
                        message: '请输入密码',
                        type: 'warning'
                    })
                }
                this.axios.post(apiUrl + '/eatApi/user/login', this.options).then(res => {
                    if (res.data.msgcode == 1) {
                        this.$message({
                            message: '登录成功',
                            type: 'success'
                        })
                        bus.$emit('checkLogin', true)
                        window.username = this.options.username
                        this.$router.go(-1)
                    } else {
                        this.$message({
                            message: res.data.msg,
                            type: 'warning'
                        })
                    }
                })
            }
        },
        beforeRouteEnter: (to, from, next) => {
            next(vm => {
                vm.axios.get(apiUrl + '/eatApi/user/checkLogin').then(res => {
                    var data = res.data
                    if (data.msgcode == 1) {
                        vm.$message({
                            message: '你已登录账号 ' + data.username,
                            type: 'success'
                        })
                        // setTimeout(() => {
                        //     vm.$router.go(-1)
                        // }, 0)
                    }
                }).catch(err => {
                    console.log(err)
                })
            })
        },
        mounted() {
        }
    }

</script>

<style scoped>
    .row {
        margin: 15px 0;
    }

    .row .code-ipt {
        width: 50%;
        float: left;
    }

    .row .get-code {
        float: right;
    }

    .row .sure {
        width: 100%;
    }

    .row .go-reg {
        cursor: pointer;
        text-align: right;
        color: #475669;
    }
</style>


